import React, { Fragment } from 'react';
import {IGallery} from "../../entity/gallery.class";
import Gallery from "./art.gallery"

interface Props{
    gallerie:Array<IGallery>,
    info:string|undefined,
    setsections:Function
}

const Index:React.FC<Props> = props => {
    
    props.setsections(
        <Fragment>
            <Gallery 
            id={0} 
            gal={props.gallerie[0]} 
            last={false} 
            key={"news0"}/>
          {props.gallerie.map((g,i) => {
            return <Gallery 
            id={(i+1)} 
            gal={g} 
            last={i+1 === props.gallerie.length?true: false} 
            key={g.titre+(i+1)}/>
          })}
        </Fragment>
      );
  return (
    <Fragment>
        <a href={"#gallery0"} className="BtnNext"><i className="fas fa-arrow-down"></i></a>
        <div className="artIndexInfo">
            <div className="artTitle">
                <h1>Lecointe<br/>Christine</h1>
            </div>
            <div className="artContent">
                <p>{props.info}</p>
            </div>
            
        </div>
        <div className="artLinks">
            <a href={"#gallery0"} >Nouveautés</a>
            {props.gallerie.map((g,i) => {
                return (<a href={"#gallery"+(i+1)} key={"link"+i}>{g.titre}</a>);
            })}
        </div>
        
        
    </Fragment>
  );
}

export default Index;
